"use client"
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { Button } from "../ui/button";
import { CheckCircle2, ChevronLeft, ChevronRight, Lock, AlertCircle, Building, Mail, User, Info, Key, Building2 } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion"
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "../ui/form";
import { Input } from "../ui/input";
import { zodResolver } from "@hookform/resolvers/zod";
import { type SignUpRequest, signUpSchema } from "@/models/auth.model";
import { cn } from "@/lib/utils";
import { PasswordStrengthMeter } from "../password-strength-meter";
import Link from "next/link";

const steps = [
	{ id: "step-1", icon: null, name: "Cadastre-se", fields: [] },
	{ id: "step-2", icon: Info, name: "Informações Pessoais", fields: ["name", "email"] },
	{ id: "step-3", icon: Key, name: "Segurança", fields: ["password", "confirmPassword"] },
	{ id: "step-4", icon: Building2, name: "Empresa", fields: ["company"] },
	{ id: "step-5", icon: null, name: "Conclusão", fields: [] },
  ]

export const SignUpForm = () => {
	const [currentStep, setCurrentStep] = useState(0)
  	const [isSubmitting, setIsSubmitting] = useState(false)
  	const [submissionStatus, setSubmissionStatus] = useState<"idle" | "success" | "error">("idle")
  	const [passwordStrength, setPasswordStrength] = useState(0)

  	const form = useForm<SignUpRequest>({
    	mode: "onChange",
    	criteriaMode: "all",
		resolver: zodResolver(signUpSchema),
		defaultValues: {
			name: "",
			email: "",
			password: "",
			confirmPassword: "",
			company: "",
		},
  	})

	const { trigger, getFieldState, formState, watch } = form
	const { errors, isValid } = formState

  	const password = watch("password")

  	React.useEffect(() => {
		if (password) {
		let strength = 0

		// Length check
		if (password.length >= 8) strength += 1

		// Contains lowercase
		if (/[a-z]/.test(password)) strength += 1

		// Contains uppercase
		if (/[A-Z]/.test(password)) strength += 1

		// Contains number
		if (/[0-9]/.test(password)) strength += 1

		// Contains special character
		if (/[^a-zA-Z0-9]/.test(password)) strength += 1

		setPasswordStrength(strength)
		} else {
		setPasswordStrength(0)
		}
	}, [password])

  // Check if the current step has errors
  const currentStepHasErrors = steps[currentStep].fields.some((field) => errors[field as keyof SignUpRequest])

  const handleNext = async () => {
    const fieldsToValidate = steps[currentStep].fields as Array<keyof SignUpRequest>
    const output = await trigger(fieldsToValidate, { shouldFocus: true })

    if (!output) return

    if (currentStep < steps.length - 2) {
      setCurrentStep((step) => step + 1)
    } else if (currentStep === steps.length - 2) {
      await handleSubmit()
    }
  }

  const handleBack = () => {
    setCurrentStep((step) => step - 1)
  }

  const handleSubmit = async () => {
    const isStepValid = await trigger(steps[currentStep].fields as Array<keyof SignUpRequest>)

    if (!isStepValid) return

    setIsSubmitting(true)

    try {
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1500))

      const formData = form.getValues()
      console.log("Form submitted successfully:", formData)

      setSubmissionStatus("success")
      setCurrentStep(steps.length - 1)
    } catch (error) {
      console.error("Error submitting form:", error)
      setSubmissionStatus("error")
      setCurrentStep(steps.length - 1)
    } finally {
      setIsSubmitting(false)
    }
  }

  	return (
    	<div className="w-full max-w-md mx-auto">

      		<Form {...form}>
        		<form className="space-y-6">
          			<AnimatePresence mode="wait">
            			{currentStep === 0 && (
							<motion.div
								key="step1"
								initial={{ opacity: 0, x: 20 }}
								animate={{ opacity: 1, x: 0 }}
								exit={{ opacity: 0, x: -20 }}
								transition={{ duration: 0.3 }}
								className="space-y-4"
							>
								<div className="text-center mb-6">
									<h2 className="text-xl font-semibold">Informações Pessoais</h2>
									<p className="text-sm text-muted-foreground mt-1">Vamos começar com algumas informações básicas</p>
								</div>
								<div className="mt-4 mb-8">
									<div className="w-full grid grid-cols-4 gap-2">
										{steps.slice(0, steps.length - 1).map((step, index) => (
											<div key={step.id} className={cn("h-2 rounded-md", currentStep >= index ? "bg-blue-600" : currentStep === index ? "bg-neutral-500" :  "bg-accent")} />
										))}
									</div>
								</div>

								<FormField
									control={form.control}
									name="name"
									render={({ field }) => (
										<FormItem>
											<FormLabel className="text-foreground">Nome completo</FormLabel>
											<div className="relative">
												<User className="absolute left-3 top-2.5 h-5 w-5 text-muted-foreground" />
												<FormControl>
													<Input placeholder="Seu nome" autoComplete="name" type="text" className="pl-10" {...field} />
												</FormControl>
											</div>
											<FormMessage />
										</FormItem>
									)}
								/>

								<FormField
									control={form.control}
									name="email"
									render={({ field }) => (
										<FormItem>
										<FormLabel className="text-foreground">Email</FormLabel>
										<div className="relative">
											<Mail className="absolute left-3 top-2.5 h-5 w-5 text-muted-foreground" />
											<FormControl>
											<Input
												placeholder="john.doe@exemplo.com"
												autoComplete="email"
												type="email"
												className="pl-10"
												{...field}
											/>
											</FormControl>
										</div>
										<FormMessage />
										</FormItem>
									)}
								/>
              				</motion.div>
            )}

            {currentStep === 1 && (
              <motion.div
                key="step2"
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: -20 }}
                transition={{ duration: 0.3 }}
                className="space-y-4"
              >
                <div className="text-center mb-6">
                  <h2 className="text-xl font-semibold">Segurança da Conta</h2>
                  <p className="text-sm text-muted-foreground mt-1">Crie uma senha forte para proteger sua conta</p>
                </div>

                <FormField
                  control={form.control}
                  name="password"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-foreground">Senha</FormLabel>
                      <div className="relative">
                        <Lock className="absolute left-3 top-2.5 h-5 w-5 text-muted-foreground" />
                        <FormControl>
                          <Input placeholder="••••••••" type="password" className="pl-10" {...field} />
                        </FormControl>
                      </div>
                      <PasswordStrengthMeter strength={passwordStrength} />
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="confirmPassword"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-foreground">Confirmar Senha</FormLabel>
                      <div className="relative">
                        <Lock className="absolute left-3 top-2.5 h-5 w-5 text-muted-foreground" />
                        <FormControl>
                          <Input placeholder="••••••••" type="password" className="pl-10" {...field} />
                        </FormControl>
                      </div>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <div className="bg-muted/50 p-3 rounded-lg border text-sm">
                  <p className="font-medium mb-2">Sua senha deve conter:</p>
                  <ul className="space-y-1">
                    <li
                      className={cn(
                        "flex items-center gap-2",
                        password?.length >= 8 ? "text-green-500" : "text-muted-foreground",
                      )}
                    >
                      <CheckCircle2 className="h-4 w-4" />
                      <span>Pelo menos 8 caracteres</span>
                    </li>
                    <li
                      className={cn(
                        "flex items-center gap-2",
                        /[A-Z]/.test(password || "") ? "text-green-500" : "text-muted-foreground",
                      )}
                    >
                      <CheckCircle2 className="h-4 w-4" />
                      <span>Uma letra maiúscula</span>
                    </li>
                    <li
                      className={cn(
                        "flex items-center gap-2",
                        /[0-9]/.test(password || "") ? "text-green-500" : "text-muted-foreground",
                      )}
                    >
                      <CheckCircle2 className="h-4 w-4" />
                      <span>Um número</span>
                    </li>
                    <li
                      className={cn(
                        "flex items-center gap-2",
                        /[^a-zA-Z0-9]/.test(password || "") ? "text-green-500" : "text-muted-foreground",
                      )}
                    >
                      <CheckCircle2 className="h-4 w-4" />
                      <span>Um caractere especial</span>
                    </li>
                  </ul>
                </div>
              </motion.div>
            )}

            {currentStep === 2 && (
              <motion.div
                key="step3"
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: -20 }}
                transition={{ duration: 0.3 }}
                className="space-y-4"
              >
                <div className="text-center mb-6">
                  <h2 className="text-xl font-semibold">Informações da Empresa</h2>
                  <p className="text-sm text-muted-foreground mt-1">
                    Quase lá! Só precisamos de mais algumas informações
                  </p>
                </div>

                <FormField
                  control={form.control}
                  name="company"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-foreground">Nome da empresa (opcional)</FormLabel>
                      <div className="relative">
                        <Building className="absolute left-3 top-2.5 h-5 w-5 text-muted-foreground" />
                        <FormControl>
                          <Input placeholder="Sua empresa" type="text" className="pl-10" {...field} />
                        </FormControl>
                      </div>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <div className="bg-muted/50 p-4 rounded-lg border text-sm mt-6">
                  <p className="text-muted-foreground">
                    Esta informação é opcional, mas nos ajuda a personalizar sua experiência. Se você não trabalha em
                    uma empresa, pode deixar este campo em branco.
                  </p>
                </div>
              </motion.div>
            )}

            {currentStep === 3 && (
              <motion.div
                key="step4"
                initial={{ opacity: 0, scale: 0.95 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.3 }}
                className="space-y-6 text-center py-6"
              >
                {submissionStatus === "success" ? (
                  <>
                    <div className="w-16 h-16 bg-green-100 dark:bg-green-900/20 rounded-full flex items-center justify-center mx-auto">
                      <CheckCircle2 className="h-8 w-8 text-green-600 dark:text-green-500" />
                    </div>
                    <h2 className="text-2xl font-bold">Cadastro Concluído!</h2>
                    <p className="text-muted-foreground">
                      Sua conta foi criada com sucesso. Você receberá um email de confirmação em breve.
                    </p>
                    <Link href="/sign-in" className="mt-4 w-full">
                      Ir para o Login
                    </Link>
                  </>
                ) : submissionStatus === "error" ? (
                  <>
                    <div className="w-16 h-16 bg-red-100 dark:bg-red-900/20 rounded-full flex items-center justify-center mx-auto">
                      <AlertCircle className="h-8 w-8 text-red-600 dark:text-red-500" />
                    </div>
                    <h2 className="text-2xl font-bold">Algo deu errado</h2>
                    <p className="text-muted-foreground">
                      Não foi possível concluir seu cadastro. Por favor, tente novamente mais tarde.
                    </p>
                    <Button
                      variant="outline"
                      className="mt-4 w-full"
                      onClick={() => {
                        setSubmissionStatus("idle")
                        setCurrentStep(0)
                      }}
                    >
                      Tentar novamente
                    </Button>
                  </>
                ) : null}
              </motion.div>
            )}
          </AnimatePresence>

          {currentStep < steps.length - 1 && (
            <div className="flex justify-between mt-8">
              <Button
                type="button"
                variant="outline"
                onClick={handleBack}
                disabled={currentStep === 0 || isSubmitting}
                className={cn(currentStep === 0 && "invisible")}
              >
                <ChevronLeft className="mr-2 h-4 w-4" />
                Voltar
              </Button>

              <Button
                type="button"
                onClick={handleNext}
                disabled={currentStepHasErrors || isSubmitting}
                className={cn(
                  "relative",
                  currentStep === steps.length - 2 && "bg-gradient-to-l to-purple-600 from-blue-600",
                )}
              >
                {isSubmitting ? (
                  <span className="flex items-center">
                    <svg
                      className="animate-spin -ml-1 mr-2 h-4 w-4 text-white"
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                    >
                      <circle
                        className="opacity-25"
                        cx="12"
                        cy="12"
                        r="10"
                        stroke="currentColor"
                        strokeWidth="4"
                      ></circle>
                      <path
                        className="opacity-75"
                        fill="currentColor"
                        d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                      ></path>
                    </svg>
                    Processando
                  </span>
                ) : (
                  <>
                    {currentStep === steps.length - 2 ? (
                      <>
                        <div className="absolute w-7 h-7 left-2 top-1/2 -translate-y-1/2 rounded-full hidden group-hover:flex items-center justify-center bg-white/20">
                          <Lock className="opacity-60 h-4 w-4" />
                        </div>
                        <span>Finalizar Cadastro</span>
                      </>
                    ) : (
                      <>
                        <span>Próximo</span>
                        <ChevronRight className="ml-2 h-4 w-4" />
                      </>
                    )}
                  </>
                )}
              </Button>
            </div>
          )}
        </form>
      </Form>
    </div>
  )
}
